export default {
	props:['modelData','initIndex'],
	data() {
		return {
			
		}
	},
	computed:{
		getModelData(){
			return this.modelData
		},
		formatBe(){
			return this.modelData
					?this.modelData.length>4?true:false
					:false
		},
		formatIndex(){
			return this.initIndex
		}
	},
	methods: {
		tap(id){
			this.$emit("change",id);
		}
	},
	watch:{
		getModelData(n){
			this.$emit('tabInit',n[0].typeId)
		}
	}
}
